var sourceType = [
	['camera'],
	['album'],
	['camera', 'album']
]
var sizeType = [
	['compressed'],
	['original'],
	['compressed', 'original']
]
export default {
	data() {
		return {
			url:'',
			product:'',
			qty:'',
			myqty:1,
			sessionId:'',
			imageList: [],
			sourceTypeIndex: 2,
			sourceType: ['拍照', '相册', '拍照或相册'],
			sizeTypeIndex: 2,
			sizeType: ['压缩', '原图', '压缩或原图'],
			countIndex: 8,
			count: [1, 2],
			uploadData:[]
		}
	},
	onLoad(option) {
		this.url = option.url
		this.qty = option.qty
		this.product = option.product
		this.sessionId = option.sessionId
	},
	methods: {
		// 数量
		reduce(){
			if(this.myqty > 1){
				this.myqty --
			}else{
				uni.showToast({
					title:'数量低于范围~',
					icon:'none'
				})
			}
		},
		
		// 增加
		add(){
			if(this.myqty < this.qty){
				this.myqty ++
			}else{
				uni.showToast({
					title:'供大于求了~',
					icon:'none'
				})
				return
			}
		},
		
		// 选择图片
		chooseImage: async function() {
			if (this.imageList.length === 9) {
				return;	
			}
			uni.chooseImage({
				sourceType: sourceType[this.sourceTypeIndex],
				sizeType: sizeType[this.sizeTypeIndex],
				count: this.imageList.length + this.count[this.countIndex] > 9 ? 9 - this.imageList.length : this.count[this.countIndex],
				success: (res) => {
					this.imageList = this.imageList.concat(res.tempFilePaths);
				},
			})
		},
		// 预览图片
		previewImage: function(e) {
			var current = e.target.dataset.src
			uni.previewImage({
				current: current,
				urls: this.imageList
			})
		},
		
		// 删除选中的图片
		del(index){
			uni.showModal({
			    title: '提示',
			    content: '是否要删除该图片',
			    success: (res)=> {
			        if (res.confirm) {
						this.imageList.splice(index,1)
			        }
			    }
			});
		},
		
		// 循环上图
		async uploadImages(url){
			uni.showLoading({
				title:'上传中'
			})
			uni.uploadFile({
				url:'https://www.nssnail.com/file/uploadWxFile',
				filePath:url,
				name:'file',
				success: (res) => {
					var resdata = JSON.parse(res.data)
					this.uploadData.push(resdata.data)
					if(this.uploadData.length == this.imageList.length){
						uni.request({
							url:'https://www.nssnail.com/wx/donateGoods',
							method:'POST',
							data:{
								fileList:this.uploadData,
								product:this.product,
								qty:this.myqty,
								sessionId:this.sessionId
							},
							success: (res) => {
								if(res.data.stat == 1){
									setTimeout(function () {
									    uni.hideLoading();
									}, 2000);
									uni.showToast({
										title:'申请成功',
										icon:'success',
										success:()=>{
											setTimeout(function(){
												uni.navigateBack({
													delta:1
												})
											},2000);
										}
									})
								}else{
									uni.showToast({
										title:res.data.message,
										icon:'none'
									})
								}
							}
						})
					}
					
				}
			})
		},
		
		// 上传图片
		async sure(){
			if(this.imageList.length==0){
				uni.showToast({
					title:'请上传物品图片',
					icon:'none'
				})
			}else{
				let arr = []
				for (let k in this.imageList) {
					arr[k] = this.uploadImages(this.imageList[k])
				}
				return arr;
			}
		},
	}
}
